// frontend/pages/admin/payments.js
import { useEffect, useState } from "react";
import { useRouter } from "next/router";
import api from "../../utils/api";

export default function AdminPaymentsPage() {
  const [payments, setPayments] = useState([]);
  const [loading, setLoading] = useState(true);
  const router = useRouter();

  useEffect(() => {
    const fetchPayments = async () => {
      try {
        const { data } = await api.get("/api/payment/all");
        if (data.success) {
          setPayments(data.payments);
        }
      } catch (err) {
        console.error(err.response?.data || err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchPayments();
  }, []);

  if (loading) return <p className="p-6 text-center">Loading payments...</p>;

  if (payments.length === 0) return <p className="p-6 text-center">No verified payments yet.</p>;

  return (
    <div className="min-h-screen p-6 bg-gray-100">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-3xl font-bold text-center flex-1">Verified Payments</h1>
        <button
          onClick={() => router.back()}
          className="ml-auto px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition"
        >
          Back
        </button>
      </div>

      <div className="overflow-x-auto">
        <table className="min-w-full bg-white rounded-xl shadow">
          <thead>
            <tr className="bg-gray-200 text-left">
              <th className="p-3 border-b">Name</th>
              <th className="p-3 border-b">Email</th>
              <th className="p-3 border-b">Amount (BDT)</th>
              <th className="p-3 border-b">Transaction ID</th>
              <th className="p-3 border-b">Date</th>
            </tr>
          </thead>
          <tbody>
            {payments.map((p) => (
              <tr key={p._id} className="border-b hover:bg-gray-50">
                <td className="p-3">{p.name || "N/A"}</td>
                <td className="p-3">{p.email}</td>
                <td className="p-3">{p.price}</td>
                <td className="p-3">{p.trxID}</td>
                <td className="p-3">{new Date(p.createdAt).toLocaleString()}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}
